body {
  background: linear-gradient(66deg, rgba(146,237,170,1) 55%, rgba(240,188,3,1) 93%);
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  align-items: center;
  justify-content: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root{
  display: flex;
  align-items: center;
  justify-content: center;
}
 h1{

  text-transform: uppercase;
 }
.container{
  display: flex;
  align-items: center;
  justify-content: center;
  width:100%;
  height:300px;
  background-color: #FFFFFF;
  border: 2px solid black;
  border-radius: 5px;
}

.Text{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.Text span {
  font-size: 30px;
  text-transform: capitalize;
}

p{
  font-size: 30px;
}

.search{
  margin-top:10px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  justify-content: center;
  align-items: center;
}

.search input{
  font-size: 20px;
  color: black;
  padding: 20px;
  background-color: rgb(243, 193, 242);
}
.search input:focus{
  background-color: rgb(228, 231, 234);
}

.search button{
  background-color: rgb(40, 10, 67);
  border-radius: 5px;
  color: white;
  padding: 10px;
}